"use client"

import { Card } from "@/components/ui/card"
import { useEffect, useRef, useState } from "react"
import { ChevronDown } from "lucide-react"

const agendaItems = [
  {
    phase: "THE WHY",
    title: "Kỷ nguyên AI và Vị thế mới của người Việt trẻ",
    keynotes: [
      "Phân tích Nghị quyết 57 & Bối cảnh thế giới.",
      "Khẳng định: AI là cơ hội duy nhất để Việt Nam đi tắt đón đầu, và là ngành nghề mới nỗi trong thời kỳ mới.",
      "Sứ mệnh của Bách Khoa trong việc phát triển tài năng kỹ sư AI từ cấp 3.",
    ],
  },
  {
    phase: "THE REALITY",
    title: "Chân dung Kỹ sư AI 2030: Họ là ai, Lương bao nhiêu? Cơ hội nghề nghiệp ở đâu?",
    keynotes: [
      "Các cơ hội nghề nghiệp, và độ phát triển của AI trong thời đại mới.",
      "Show bảng lương thực tế & lộ trình thăng tiến.",
      "Chia sẻ thật về cơn khát và nhu cầu kỹ sư AI hiện tại và trong 5 năm nữa.",
    ],
  },
  {
    phase: "LỘ TRÌNH 360 GIỜ",
    title: "Giúp học sinh có nền tảng tốt chuẩn quốc tế, dựa theo chương trình của NVIDIA",
    keynotes: [
      
      "Phân tích những kiến thức nền tảng & tính thực tế của khóa học mang đến.",
    ],
  },
  {
    phase: "NGƯỜI ĐI TRƯỚC 2-3 năm",
    title: "Là học sinh, cảm thấy rất đáng để bắt đầu từ cấp 3",
    keynotes: [
      "Chia sẻ một bạn trẻ học chuyên ngành AI ở Việt Nam về cuộc đua về AI ở chính ngôi trường của bạn.",
      " Truyền cảm hứng: Nếu mình làm được, bạn cũng làm được.",
      "Q&A trực tiếp với các bạn học sinh tiên phong",
    ],
  },
]

export function AgendaSection() {
  const [isVisible, setIsVisible] = useState(false)
  const [expandedIndex, setExpandedIndex] = useState<number | null>(null)
  const sectionRef = useRef<HTMLElement>(null)

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true)
        }
      },
      { threshold: 0.1 },
    )

    if (sectionRef.current) {
      observer.observe(sectionRef.current)
    }

    return () => observer.disconnect()
  }, [])

  const toggleExpand = (index: number) => {
    setExpandedIndex(expandedIndex === index ? null : index)
  }

  return (
    <section
      ref={sectionRef}
      className="py-16 md:py-24 bg-gradient-to-br from-[#1e3a5f] via-[#1a3354] to-[#0f2744] relative overflow-hidden"
    >
      <div className="absolute inset-0 opacity-10">
        <div className="absolute top-20 left-10 w-32 h-32 bg-blue-400 rounded-full blur-3xl"></div>
        <div className="absolute bottom-20 right-10 w-40 h-40 bg-blue-500 rounded-full blur-3xl"></div>
      </div>

      <div className="container mx-auto px-4 relative z-10">
        <h2
          className={`text-3xl md:text-4xl lg:text-5xl font-bold text-center mb-16 md:mb-20 text-white transition-all duration-700 tracking-wide ${isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-10"}`}
        >
          AGENDA
        </h2>

        <div className="grid lg:grid-cols-1 gap-8 md:gap-12 max-w-7xl mx-auto items-center">
          <div className="space-y-4 md:space-y-5 mt-10 lg:mt-16">
            {agendaItems.map((item, index) => (
              <Card
                key={index}
                className={`group relative overflow-hidden bg-white hover:shadow-2xl transition-all duration-500 hover:-translate-y-2 border-l-4 border-[#2563eb] cursor-pointer ${
                  isVisible ? "opacity-100 translate-x-0" : "opacity-0 -translate-x-10"
                }`}
                style={{ transitionDelay: `${index * 150}ms` }}
                onClick={() => toggleExpand(index)}
              >
                <div className="absolute inset-0 bg-gradient-to-r from-blue-50 to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-500"></div>

                <div className="relative">
                  <div className="flex gap-3 md:gap-4 p-6 md:p-5">
                    <div className="flex-shrink-0 w-10 h-10 md:w-12 md:h-12 bg-gradient-to-br from-[#2563eb] to-[#1d4ed8] text-white rounded-xl flex items-center justify-center font-bold text-lg md:text-xl shadow-lg group-hover:scale-110 transition-transform duration-500">
                      0{index + 1}
                    </div>
                    <div className="flex-1">
                      <h3 className="font-bold text-base md:text-lg mb-1 md:mb-2 text-[#1e3a5f] group-hover:text-[#2563eb] transition-colors duration-300">
                        {item.phase}
                      </h3>
                      <p className="text-gray-700 text-sm md:text-base leading-relaxed">{item.title}</p>
                    </div>
                    <ChevronDown
                      className={`flex-shrink-0 w-5 h-5 md:w-6 md:h-6 text-[#2563eb] transition-transform duration-300 ${
                        expandedIndex === index ? "rotate-180" : ""
                      }`}
                    />
                  </div>

                  <div
                    className={`overflow-hidden transition-all duration-500 ease-in-out ${
                      expandedIndex === index ? "max-h-96 opacity-100" : "max-h-0 opacity-0"
                    }`}
                  >
                    <div className="px-6 pb-6 md:px-5 md:pb-5 pt-2 border-t border-gray-200 bg-gradient-to-br from-blue-50 to-white">
                      <h4 className="font-semibold text-[#1e3a5f] mb-3 text-sm md:text-base">Nội dung chi tiết:</h4>
                      <ul className="space-y-2">
                        {item.keynotes.map((keynote, kIndex) => (
                          <li key={kIndex} className="flex gap-2 text-gray-700 text-sm md:text-base">
                            <span className="text-[#2563eb] font-bold">•</span>
                            <span>{keynote}</span>
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>
              </Card>
            ))}
          </div>
        </div>
      </div>
    </section>
  )
}
