"use client"

import { Card } from "@/components/ui/card"
import { useEffect, useRef, useState } from "react"
import { Award, Star, Trophy } from "lucide-react"

const testimonials = [
  {
    name: "Nguyễn Việt Hưng",
    title: "Sinh viên xuất sắc - Khoa KHMT, Đại học Bách Khoa Hà Nội",
    school:
      "Cựu học sinh Chu Văn An \nGiải Nhì Thành phố tiếng Anh\nThủ khoa 5 khối THPTQG\nIelts 8.0 và SAT 1560\nKhoa học dữ liệu - Trí tuệ nhân tạo HUST",
    image: "/images/viet-hung.jpg",
    achievements: ["IELTS 8.0", "SAT 1560", "Thủ khoa 5 khối"],
  },
]

export function TestimonialsSection() {
  const [isVisible, setIsVisible] = useState(false)
  const sectionRef = useRef<HTMLElement>(null)

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true)
        }
      },
      { threshold: 0.1 },
    )

    if (sectionRef.current) {
      observer.observe(sectionRef.current)
    }

    return () => observer.disconnect()
  }, [])

  return (
    <section
      ref={sectionRef}
      className="py-16 md:py-24 bg-gradient-to-br from-blue-50 via-white to-blue-50 relative overflow-hidden"
    >
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <div className="absolute top-10 left-10 w-32 h-32 bg-blue-200 rounded-full blur-3xl opacity-30 animate-pulse"></div>
        <div
          className="absolute bottom-20 right-20 w-40 h-40 bg-indigo-200 rounded-full blur-3xl opacity-30 animate-pulse"
          style={{ animationDelay: "1s" }}
        ></div>
      </div>

      <div className="container mx-auto px-4 relative z-10">
        <div className="text-center mb-12 md:mb-16">
          <div
            className={`inline-flex items-center gap-2 px-4 py-2 bg-gradient-to-r from-blue-600 to-indigo-600 text-white rounded-full text-sm font-semibold mb-4 transition-all duration-700 ${isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-10"}`}
          >
            <Star className="w-4 h-4" />
            <span>Diễn giả đặc biệt</span>
          </div>
          <h2
            className={`text-3xl md:text-5xl font-bold bg-gradient-to-r from-blue-600 via-indigo-600 to-purple-600 bg-clip-text text-transparent mb-4 transition-all duration-700 ${isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-10"}`}
            style={{ transitionDelay: "100ms" }}
          >
            KHÁCH MỜI
          </h2>
          <p
            className={`text-gray-600 text-base md:text-lg transition-all duration-700 ${isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-10"}`}
            style={{ transitionDelay: "200ms" }}
          >
            Gặp gỡ những người truyền cảm hứng của chúng ta
          </p>
        </div>

        <div className="flex flex-col sm:flex-row flex-wrap justify-center gap-6 md:gap-8 max-w-5xl mx-auto">
          {testimonials.map((testimonial, index) => (
            <div
              key={index}
              className={`relative group transition-all duration-700 ${
                isVisible ? "opacity-100 translate-y-0" : "opacity-0 translate-y-10"
              }`}
              style={{ transitionDelay: `${300 + index * 100}ms` }}
            >
              {/* Gradient border effect */}
              <div className="absolute -inset-0.5 bg-gradient-to-r from-blue-600 via-indigo-600 to-purple-600 rounded-2xl blur opacity-30 group-hover:opacity-60 transition duration-500 animate-pulse"></div>

              <Card className="relative p-8 md:p-10 text-center bg-white shadow-2xl hover:shadow-3xl transition-all duration-500 hover:-translate-y-3 w-full sm:w-80 md:w-96 rounded-2xl">
                {/* Profile image with decorative ring */}
                <div className="relative w-44 h-44 md:w-52 md:h-52 mx-auto mb-6">
                  <div className="absolute inset-0 bg-gradient-to-r from-blue-500 via-indigo-500 to-purple-500 rounded-full animate-spin-slow opacity-75"></div>
                  <div className="absolute inset-2 rounded-full overflow-hidden bg-white shadow-xl">
                    <img
                      src={testimonial.image || "/placeholder.svg"}
                      alt={testimonial.name}
                      className="w-full h-full object-cover"
                    />
                  </div>
                  {/* Trophy badge */}
                  <div className="absolute -top-2 -right-2 bg-gradient-to-br from-yellow-400 to-orange-500 text-white p-3 rounded-full shadow-lg transform rotate-12 group-hover:rotate-0 transition-transform duration-300">
                    <Trophy className="w-6 h-6" />
                  </div>
                </div>

                {/* Name with gradient */}
                <h3 className="font-bold text-xl md:text-2xl mb-2 bg-gradient-to-r from-blue-600 to-indigo-600 bg-clip-text text-transparent">
                  {testimonial.name}
                </h3>

                {/* Title */}
                <p className="text-sm md:text-base text-gray-600 font-medium mb-4 px-2">{testimonial.title}</p>

                {/* Achievement badges */}
                <div className="flex flex-wrap gap-2 justify-center mb-4">
                  {testimonial.achievements.map((achievement, idx) => (
                    <span
                      key={idx}
                      className="px-3 py-1 bg-gradient-to-r from-blue-500 to-indigo-500 text-white text-xs font-semibold rounded-full shadow-md"
                    >
                      {achievement}
                    </span>
                  ))}
                </div>

                {/* Divider */}
                <div className="w-16 h-1 bg-gradient-to-r from-blue-600 to-indigo-600 mx-auto mb-4 rounded-full"></div>

                {/* School info */}
                <div className="flex items-start justify-center gap-2 mb-4">
                  <Award className="w-5 h-5 text-blue-600 flex-shrink-0 mt-1" />
                  <p className="text-sm md:text-base text-gray-700 whitespace-pre-line text-left leading-relaxed">
                    {testimonial.school}
                  </p>
                </div>

                {/* Bottom accent */}
                <div className="absolute bottom-0 left-0 right-0 h-1 bg-gradient-to-r from-blue-600 via-indigo-600 to-purple-600 rounded-b-2xl"></div>
              </Card>
            </div>
          ))}
        </div>
      </div>
    </section>
  )
}
